export function getChartOption(chart, data) {
  let option = {
    xAxis: [],
    yAxis: [],
    series: []
  }
  chart.sqlConfig.x.forEach(xl => {
    const xData = data.map(i => i[xl.finalAliasName])
    option.xAxis.push({
      type: 'category',
      data: xData
    })
  })

  chart.sqlConfig.y.forEach(yl => {
    const yData = data.map(i => i[yl.finalAliasName])
    option.yAxis.push({
      type: 'value'
    })
    option.series.push({
      type: map[chart.chartType],
      data: yData
    })
  })
  return option
}

const map = {
  C5: 'bar',
  C4: 'line',
  c6: 'pie'
}

